define('ember-riak-explorer/routes/riak-ping', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Route.extend({
        queryParams: {
            nodeId: {
                refreshModel: true
            }
        },

        model: function model(params) {
            return this.explorer.getNodePing(params.nodeId);
        }
    });

});