define('ember-riak-explorer/routes/error/cluster-not-found', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    exports['default'] = Ember['default'].Route.extend({
        queryParams: {
            cluster_id: {
                refreshModel: true
            }
        },

        model: function model(params) {
            return {
                clusterId: params.cluster_id
            };
        }
    });

});